<template>
  <div>
    <nav-head title="历史记录" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="tab-wrap"><div @click="switchType(1)" :class="{'active':type===1}"><span>充值记录</span></div><div @click="switchType(5)" :class="{'active':type===5}"><span>提现记录</span></div></div>
    <!--<ul class="thead"><li>时间</li><li>事件内容</li><li>金额变动</li></ul>-->
    <Loadmore :top-method="loadTop" ref="loadmore" spaceBetween="44">
      <div
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
        infinite-scroll-immediate-check="false"
      >
        <div class="tbody" :style="{minHeight: `${Device.height-46-44}px`}">
          <div v-for="item in History[type]" v-bind:key="item.id">
            <span class="time">{{item.create_time | dateFormat('yyyy-MM-dd hh:mm:ss')}}</span>
            <span class="description">{{item.description}}</span>
            <span class="value" v-bind:class="{ 'color-green': item.diamond.indexOf('-') >= 0 }">{{item.diamond.indexOf('-') >= 0 ? item.diamond : "+"+item.diamond}}</span>
          </div>
        </div>
        <div v-show="loading!==1" style="color: #999;text-align: center;line-height: 35px;">
          加载中•••
        </div>
      </div>
    </Loadmore>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { mapActions, mapState } from 'vuex'
import { GET_HISTORY } from '../../../vuex/Mine'
import Loadmore from '../../../components/loadmore'

export default {
  name: 'history',
  data: function () {
    return {
      loading: null,
      type: 1
    }
  },
  computed: {
    ...mapState({
      Device: state => state.mine.Device,
      History: state => state.mine.History
    })
  },
  methods: {
    switchType (type) {
      this.type = type
      this.getHistory({
        type: this.type,
        index: 1,
        func: () => {
          if (this.History[this.type].length >= 10) {
            this.loadMore()
          } else {
            this.loading = 1
          }
        }
      })
    },
    loadTop () {
      // 刷新数据
      this.getHistory({
        type: this.type,
        index: 1,
        func: () => {
          setTimeout(() => {
            this.$refs.loadmore.onTopLoaded()
            if (this.loading) this.loading = false
            if (this.History[this.type].length >= 10) {
              this.loadMore()
            } else {
              this.loading = 1
            }
          }, 500)
        }
      })
    },
    loadMore () {
      // 加载更多数据
      this.loading = true
      if (this.History[this.type].length > 0 && this.History[this.type].length % 10 !== 0) return
      const leng = this.History[this.type].length
      this.getHistory({
        type: this.type,
        index: 2,
        func: () => {
          if (leng !== this.History[this.type].length && this.History[this.type].length % 10 === 0) {
            // 长度变化且能被整除
            this.loading = false
          } else {
            // 长度没变拿不到新数据了,永远禁止上拉加载
            this.loading = 1
          }
        }
      })
    },
    ...mapActions({
      getHistory: GET_HISTORY
    })
  },
  created () {
    this.getHistory({
      type: 1,
      index: 1,
      func: () => {
        if (this.History[this.type].length >= 10) {
          this.loadMore()
        } else {
          this.loading = 1
        }
      }
    })
  },
  components: {
    Loadmore,
    NavHead
  }
}
</script>
